img{

    width: 100px;
    height: 120px;
    margin-right: 1em;
}

.navbar{

    display: inline-grid;
    justify-content: space-around;
    
    background-color: black;
    padding: 1em;
    width: 102%;
}


.list{
    float: right;
    display: flex;
    list-style: none;
    align-items: center;
    margin-top: 7%;
 
    
}
.item{
    margin-right:1em ;
    
}

.item a{
    color: azure;
    text-decoration: none;

}

.item a:hover {

    color: aliceblue;
    text-decoration: none;
}

.item {

    grid-column-start: 3;
    
}


