.Container {
        width: 60%;
        display: grid;
        grid-template-columns: auto;
        gap: 50px;
        margin-left: 20%;
        padding: 3%;
        background-color: black;
        margin-top: 1%;
        margin-block-end: 1%;
        border-radius: 10px;
}

.Container h1{

        color: white;
        text-align: center;
}
.Container input{

        width: 100%;
        height: 60px;
}
.Container textarea{
        height: 100px;
        width: 100%;

}
 
.item{

padding: 10px 0px;

}
