.Container{
    
    width: 1200px;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    flex-wrap: wrap;
    border-radius: 30px;
    background-color: black;
}

.min-height {

    min-height: 75%;

}
.start {

   justify-content: flex-start;

}

.column {

    flex-direction: column;
    justify-content: flex-start;

}